body{
    background-color: $backgroundColor;
}

a{
    color: $mainTextColor;
}

a:hover{
    text-decoration: none !important;
    color: $backgroundColor !important;
}

.main{
    .mainText{
        @include header();
        font-size: $nameFontSize;
        @include flexColumn();
    }
    .centerArrow{
        width: 100%;
        margin-top: $marginTop;
        @include middle1();

        .arrow{
            font-size: 6.5vh;
            color: $extraColor;
            @include middle();
            animation-duration: 2s;
            animation-iteration-count: infinite;
            transform-origin: bottom;
        }

        .bounce {
            animation-name: bounce;
            animation-timing-function: linear;
        }
        @keyframes bounce {
            0%   { transform: translateY(0); }
            50%  { transform: translateY(-10px); }
            100% { transform: translateY(0); }
        }
    }
    .special{
        @include secondText();
    }

    .specialLink{
        @include secondText();
        padding: $titleFontSize;
        font-size: $marginButtonTop;

        .iconPosition{
            margin-right: $helloTextFontSize;
            margin-left: $helloTextFontSize;
        }

        .fa:hover{
            color: $extraColor;
        }
    }
}

@media only screen and (max-width: 920px) {
    .socialIcon{
        display: none !important;
    }

    .mainText{
        font-size: $marginTop !important;
    }

    .centerArrow{
        margin-top: $marginTop !important;
    }
}

@media only screen and (max-width: 450px) {
    .centerArrow{
        margin-top: $marginButtonTop !important;
    }
}

@media only screen and (max-width: 539px) and (min-width: 385px){
    .mainText{
        margin-top: $marginCursorTop !important;
    }

    .special{
        margin-top: $futureTitleFontSize !important;
    }
}

@media only screen and (min-width: 600px) and (max-width: 729px){
    .mainText{
        margin-top: $marginCursorTop !important;
    }
}